import React, { Component } from 'react';
import { connect } from 'react-redux';

import confirm from '../../components/Confirm/Confirm';
import {
    TIP,
    CANCEL_CONNECT_TIP,
    YES,
    NO,
    CANCEL_CONNECT,
    RECONNECT,
    AUTHORIZE_SERVICES_TITLE,
} from '../../config/copywriting';
import { APP_NAMES } from '../../config/const';
import WechatUnificationService from '../../service/WechatUnificationService';
import {
    ClyEnterAuthorizationPage,
    ClyClickCancelAssociate,
    ClyShowCancelAssociateDialog,
    ClyClickSure,
    ClyAssociateClickCancle,
    ClyClickAssociate,
} from '../../countly/countlyPoints';

import ComponentLoading from '../../components/ComponentLoading';

import styles from './index.module.scss';


class ServiceList extends Component {

    state = {
        loading: true,
        devices: [
            // {
            //     deviceKey: '',
            //     name: '',
            //     type: '',
            //     devicesessionList: {
            //         consumerKey: '',
            //         createTime: '',
            //         lastaccesstime: '',
            //         appname: '',
            //         channelid: '',
            //         deviceidentifier: '',
            //         devicedescription: '',
            //         isconnected: true,
            //     }  
            // }
        ]
    }

    componentWillReceiveProps(nextProps) {
        const { wechatSessionId } = nextProps;
        this.getServiceList(wechatSessionId);
    }

    componentDidMount() {
        const { wechatSessionId } = this.props;
        this.getServiceList(wechatSessionId);
        ClyEnterAuthorizationPage();
    }

    getServiceList = (wechatSessionId) => {
        if (!wechatSessionId) this.props.history.push('/account-info');
        if (wechatSessionId) {
            WechatUnificationService.ListUserDevices(wechatSessionId)
                .then((data) => {
                    console.log(data.toObject())
                    this.setState({
                        devices: data.toObject().userdeviceList,
                        loading: false,
                    });
                })
                .catch((e) => {
                    console.warn(e);
                });
        }
    }

    confirmCancelConnect = (deviceSession) => {
        ClyClickCancelAssociate();
        ClyShowCancelAssociateDialog();
        confirm({
            title: TIP,
            content: CANCEL_CONNECT_TIP,
            operates: [{
              text: YES,
              onClick: () => this.cancelConnect(deviceSession),
            },
            {
              text: NO,
              main: true,
              onClick: ClyAssociateClickCancle,
            }]
          })
    }

    cancelConnect = ({ channelid }) => {
        ClyClickSure();
        WechatUnificationService.Disconnect(channelid)
            .then((data) => {
            })
            .catch((e) => {
                console.error(e);
            })
            .finally(() => {
                window.wx.closeWindow();
            })
    }

    reconnect = (deviceSession) => {
        ClyClickAssociate();
        const { channelid, deviceidentifier, devicedescription } = deviceSession;
        WechatUnificationService.Reconnect(channelid, deviceidentifier, devicedescription)
            .then((data) => {
            })
            .catch((e) => {
                console.error(e);
            })
            .finally(() => {
                window.wx.closeWindow();
            })
    }

    getConnectButton = (deviceSession) => {
        const { isconnected, channelid } = deviceSession;
        console.log(isconnected, typeof isconnected)
        if (typeof isconnected !== 'boolean' || !channelid) return
        const msg = isconnected ? CANCEL_CONNECT : RECONNECT;
        const cb = isconnected ? this.confirmCancelConnect : this.reconnect;
        return (
            <div className={styles.rightBlock}>
                <button className={styles.connectBtn}
                        onClick={() => cb(deviceSession)}
                >{msg}</button>
                <div className={styles.direct}></div>
            </div>
        )
    }

    getProperClassname = (type) => {
        switch(type) {
            case APP_NAMES.MYYXBJ:
                return 'myyxbj';
            case APP_NAMES.MICRO_LIST:
                return 'microlist';
            default:
                return 'evernote'
        }
    }

    render() {
        const { loading, devices } = this.state;
        return (
            <div className={styles.serviceList}>
                <div className={styles.title}>
                    <span>{AUTHORIZE_SERVICES_TITLE}</span>
                </div>
                {loading && <ComponentLoading />}
                <div className={styles.block}>
                    {devices.reduce((acc, item) => {
                        const { name, devicekey, type, devicesessionList = {} } = item;
                        const iconClass = `${styles.avator} ${styles[this.getProperClassname(type)]}`; // todo base on type change classname
                        const lines = devicesessionList.map((line) => { // devicesessionList决定了渲染的行数，同一个app有可能有多台设备登陆
                            const { lastaccesstime, appname, devicedescription } = line;
                            return (
                                <div className={styles.line} key={devicekey} >
                                    <div className={styles.leftBlock}>
                                        <div className={iconClass}></div>
                                        <div className={styles.detail}>
                                            <div className={styles.name}>{type}</div>
                                            <div className={styles.detailInfo}>{devicedescription}</div>
                                            <div className={styles.detailInfo}>{lastaccesstime !== '-1' && lastaccesstime}</div>
                                        </div>
                                    </div>
                                    {this.getConnectButton(line)}
                                </div>
                            )
                        })
                        return [...acc, ...lines];
                    }, [])}
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        wechatSessionId: state.wechatSessionId,
        setWechatJsReady: state.setWechatJsReady,
    };
};
  
export default connect(
    mapStateToProps,
)(ServiceList);
